.navbar {
  .mega-dropdown {
    position: static !important; }
  .dropdown-menu.mega-menu {
    width: 100%;
    border: none;
    border-radius: 0;
    &.v-2 {
      a {
        padding: 0;
        &.news-title-2 {
          font-weight: 500;
          font-size: 1.1rem;
          line-height: 1.5;
          -webkit-transition: .2s;
          transition: .2s;
          color: #fff !important;
          &:hover {
            color: #d0d6e2 !important; } } }
      .sub-menu {
        ul {
          list-style: none;
          &.caret-style li {
            -webkit-transition: .3s;
            transition: .3s;
            &:hover {
              background-color: rgba(0, 0, 0, 0.2);
              -webkit-transition: .3s; } } }


        a.menu-item {
          color: #fff !important;
          &:hover {
            color: #fff !important; } } }
      .sub-title {
        padding-bottom: 1rem;
        margin-bottom: 1rem;
        border-bottom: 1px solid; }
      .font-small {
        font-size: 0.85rem; } } } }
